.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 25px;
  /* display: flex; */
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.resultado{
  border: rgb(102, 102, 102) 1px solid;
  border-radius: 5px;
  background: rgb(194, 224, 194);
  padding: 10px;
  color: black;
  margin-bottom: 15px;
  margin-top: 15px;
}

.grid-header {
  background: rgb(29, 141, 25);
  text-align: center;
}

.grid-container {
  /* 
  border: rgb(102, 102, 102) 1px solid;
  padding: 5px;
  */
  display: grid;
  grid-template-columns: 30% 70%;
  border-radius: 5px;
}

.form-container {
  border: rgb(102, 102, 102) 1px solid;
  display: grid;
  grid-template-columns: 30% 70%;
  border-radius: 5px;
  padding: 10px;
}

.form-item {
  text-align: left;
}

.form-item-ejemplo {
  text-align: left;
  font-size: medium;
}

.grid-item {
  text-align: left;
  border-bottom: rgb(102, 102, 102) 1px solid;
  /* background: blue;
  grid-column-start: 1; 
  grid-column-end: 5; 
  grid-row-start: 1; 
  grid-row-end: 3; */
}

.container {
  border-radius: 5px;
  padding: 10px;
  width: 750px;
  margin: 0 auto;
}

label, input{
  font-size: calc(10px + 2vmin);
}

button {
  font-size: calc(3px + 2vmin);
}